<template>
  <div class="bh-con bh-help">
    <div class="con-top">
      <div>
        <p class="title">{{msg}}</p>
        <p>
          <span>mapState: </span>
          <i>$store.state.count: {{count}}</i>
        </p>
        <p>
          <span>mapGetters: </span>
          <i>$store.getters.doneTodos: {{doneTodos}}</i>
          <i>$store.getters.doneTodosCount: {{doneTodosCount}}</i>
        </p>
        <p>
          <span>mapMutations: </span>
          <button @click="increment(2)">count</button>
        </p>
        <p>
          <span>mapActions: </span>
          <button @click="incrementAsync(2)">count</button>
        </p>
      </div>
      <div>
        <p class="title">async/await</p>
        <button @click="asyncCall">async/awaitasyncNum</button>
        <button @click="asyncCall1">async/awaitasyncNum</button>
        <div>value: {{asyncNum}}</div>
      </div>
    </div>
  </div>
</template>

<script>

  // VueDOM

  // Vuexstore""


  // mapState
  // mapState...(ECMAScript stage-3)


  import { mapState, mapGetters, mapMutations, mapActions } from 'vuex'
  // import { createNamespacedHelpers } from 'vuex'
  // const { mapState, mapGetters, mapMutations, mapActions } = createNamespacedHelpers('help')
  // createNamespacedHelpers
  export default {
    name: 'help',
    data() {
      return {
        msg: 'vuex simple demo',
        asyncNum: 0
      }
    },
    // computed: {
    //     count() {
    //         return this.$store.state.count;
    //     }
    // },
    // computed: mapState([ // statemapState
    //     'count'  // this.count  store.state.count
    // ]),
    computed: {
      localComputed() {
        // local computed
      },
      // ...mapState({ //    mapState(...)
      //     // count: state => state.count     // 
      //     // countAlias: 'count'   // 'count''state => state.count'
      //     // count(state) {      // this
      //     //     return state.count + this.msg
      //     // }
      // }),

      // statemapState
      // mapState([
      // 	'count'		// 'this.count'store.state.count
      // ]),

      // ...mapGetters([
      //     'doneTodos',    // this.doneTodos  this.$store.getters.doneTodos
      //     'doneTodosCount'
      // ])

      ...mapState('help', {
        count: 'count'
      }),
      ...mapGetters('help', {
        doneTodos: 'doneTodos',
        doneTodosCount: 'doneTodosCount'
      })
    },
    methods: {
      // this.$store.commit('xxx')mutationmapMutationsmethodsstore.commit(store)

      // mutationsstore.committype
      // 1. this.$store.commit('increment')
      // 2. this.$store.commit({type:'increment'})


      // ...mapMutations([
      //     'increment'     // this.increment()  this.$store.commit('increment')
      // ]),
      // ...mapActions([
      //     'incrementAsync'    // this.incrementAsync()  this.$store.dispatch('incrementAsync')
      // ])
      ...mapMutations('help', {
        increment: 'increment'
      }),
      ...mapActions('help', {
        incrementAsync: 'incrementAsync'
      }),

      timeout(seconds) {
        return new Promise((resolve, reject) => {
          setTimeout(() => {
            resolve('resolved');
          }, seconds);
        })
      },
      // asyncfunctionawait
      // awaitasync
      // asyncPromise
      async asyncCall() {
        console.log('calling');
        const result = await this.timeout(2000);
        console.log(result);
        this.asyncNum += 100;
      },
      // async/await
      asyncCall1() {
        console.log('calling');
        this.timeout(2000).then((data) => {
          console.log(data);
          this.asyncNum += 100;
        })
      }
    }

  }
</script>

<style lang="scss">
  .bh-help {
    height: 100%;
    overflow-y: scroll;
    .con-top {
      line-height: 40px;
      p {
        margin-bottom: 30px;
        span, i, button {
          display: block;
        }
        i {
          font-style: normal;
        }
      }
    }
    .title {
      font-weight: bold;
      color: #fabe00;
    }
    button {
      padding: 3px;
    }
  }
</style>



